.authenticate-login {
  flex-direction: column;
  gap: .5rem;
  display: flex;

  & > label {
    gap: .3rem;
    display: flex;

    & > span:first-child {
      text-align: right;
      flex: 1 2 50%;
    }

    & > span:nth-child(2) {
      text-align: left;
      flex: 50%;
    }
  }

  & input {
    width: 8rem;
    font-size: 100%;
  }

  & > button {
    align-self: center;
  }
}

.authenticate-logout {
  align-items: center;
  gap: .3rem;
  margin-bottom: .5rem;
  display: flex;
}

.with-socket {
  flex-direction: column;
  align-items: stretch;
  gap: .3rem;
  margin-bottom: .5rem;
  display: flex;

  & .with-socket-control {
    flex-direction: row;
    justify-content: flex-start;
    gap: .3rem;
    display: flex;
  }

  & .with-socket-control span:before {
    content: "[";
  }

  & .with-socket-control span:after {
    content: "]";
  }

  & .with-socket-disconnected {
    color: hsl(var(--error-hue), 100%, 40%);
  }
}

.paint {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: .3rem;
  display: flex;

  & canvas {
    border: 1px dotted gray;
  }

  & .paint-control {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: .3rem;
    display: flex;

    & label {
      gap: .1rem;
      display: flex;
    }

    & input[type="number"] {
      width: 3rem;
    }
  }
}
